<template>
  <a-modal
    :title="title"
    :width="640"
    :visible="visible"
    :confirmLoading="loading"
    @ok="
      () => {
        $emit('ok')
      }
    "
    @cancel="
      () => {
        $emit('cancel')
      }
    "
  >
    <a-spin :spinning="loading">
      <a-form-model ref="bindForm" :model="model" :rules="rules" v-bind="formItemLayout">
        <a-form-model-item label="流媒体" prop="media_server_id">
          <a-select show-search placeholder="请选择服务节点" v-model="model.media_server_id">
            <a-select-option v-for="(node, key) in mediaServerItems" :value="node" :key="key">{{
              node
            }}</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-spin>
  </a-modal>
</template>

<script>
import { getMediaServerList } from '@/api/device'

const defaultModel = {
    media_server_id: undefined
}
export default {
    props: {
        visible: {
            type: Boolean,
            default: true
        },
        loading: {
            type: Boolean,
            default: () => false
        }
    },
    data () {
        return {
            title: '绑定流媒体服务节点',
            model: defaultModel,
            mediaServerItems: {},
            formItemLayout: {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 }
            },
            rules: {
                media_server_id: [{ required: true, message: '流媒体必须选择', trigger: 'blur' }]
            }
        }
    },
    methods: {
        async loadmediaServerItems () {
          this.mediaServerItems = await getMediaServerList()
          console.log('====================================')
          console.log('mediaServerItems:', this.mediaServerItems)
          console.log('====================================')
        },
        resetForm () {
            this.model.media_server_id = defaultModel.media_server_id
            // this.$nextTick(() => {
            //    this.$refs.bindForm.resetFields()
            // })
        }
    },
    watch: {
        async visible (newValue, oldValue) {
            if (newValue) {
              this.loadmediaServerItems()
            }
        }
    }
}
</script>
<style lang="less" scoped>
.ant-select {
    min-width: 220px;
}
</style>
